import {useState,useEffect, useRef} from "react";
import '../App.css';

export default function Item({works}){
    const [isDone,setIsDone] = useState(true);
    const [work,setWork] = useState(works);
   
    function visible(){
        setIsDone(!isDone)
        // isDone == "checked=" ? setIsDone('') : setIsDone('checked');
    }

    function dataProcess(){       
        fetch(`./work/${works.id}`,{
            method:"put",
            headers:{"Content-Type":"application/json"},
            body:JSON.stringify({...work, "isDone":`${isDone}`})
        })
        .then(res => res.json())
        .then(data => {
            setWork(data);
        });  
    }

    return (
        <>
        {work && 
            // <figure>
            //     <img src={work.thum} alt={work.name} />
            //     <figcaption>
            //         {work.name} ({work.city}) 
            //         <p>
            //             <a href={work.pUrl} target="_blank">
            //                 {work.hope} <br/>
            //                 {work.pUrl}
            //             </a>
            //         </p>                   

            //         <a href={work.resume}><button>이력서 다운로드</button></a>
            //     </figcaption>
            // </figure>
            
            <tr className={work.isDone}>
                <td>{work.id}</td>
                <td>{work.name} ({work.city})</td>
                <td>{work.hope}</td>
                <td>                
                    <a href={work.pUrl} target="_blank">
                        {work.pUrl}
                    </a>
                </td>
                <td><a href={work.resume}><button>이력서 다운로드</button></a></td>
{/*                
                  <td>
                    <input type="checkbox" checked={isDone} onChange={visible} />
                    <button onClick={dataProcess}>적용</button>
                </td>            */}
            </tr>
        }
        </>
    );
}